::-webkit-scrollbar{
    display: none;

}

*{
    user-select: none;
}
body{
    background: #1c1c1c;
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* #fa4454 */
}
img {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
    }